@import url(https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
.window{position:absolute;background:yellow;top:50%;right:50%}.open{z-index:999;position:absolute;left:-150px;top:-125px;box-shadow:0px 0px 13px 5px rgba(0,0,0,0.75)}.top-content{top:150px;right:50%;width:300px;height:250px;background-color:darkgrey;padding:0px;text-align:center}.top-content p{color:#f2f2f2;margin:0px;font-size:18px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.modal{bottom:-1440px}.top-content .alert-left-text pre{text-align:left;padding:0px 0px;font-size:23px;color:#f2f2f2;font-weight:400;-webkit-user-select:none;-ms-user-select:none;user-select:none;background:limegreen;padding:10px;user-select:none}#deleteModal,#close-btn{position:absolute;top:10px;right:20px;font-size:16px;color:darkgrey;cursor:pointer;padding:0 6px 0 6px;box-shadow:0px 0px 5px 5px rgba(0,0,0,0.35)}#close-btn{color:#bd0404;position:absolute;top:210px;right:85px}#deleteModal:hover{color:red;box-shadow:0px 0px 5px 5px rgba(0,0,0,0.55)}#close-btn:hover{color:darkgrey;box-shadow:0px 0px 5px 5px rgba(0,0,0,0.55)}.alertFlame{color:limegreen}body #open{left:0px;width:100%;min-height:100%;background-color:rgba(0,0,0,0.5);overflow:hidden;position:fixed;top:0px}

